import React from 'react';

const Main = () => (
    <div>
    <div id="mainImg" className="bg-header d-none d-md-none d-lg-block"></div>
    <div className="d-xs-block d-lg-none">
        <img className="img-fluid" src="img/header.jpg"/>
    </div>
    </div>
);

export default Main;



